import React, {useState} from 'react'
import useForm from './useForm'
import validate from './validateInfo'
import './scss/Forms.css'


const FormSignup = ({submitForm}) => {
    const {handleChange, values, handleSubmit, errors} = useForm(submitForm, validate);


    return (
        <div className="background">
            <div className="form__content__right container">
                <form className="form" onSubmit={handleSubmit} name="form">
                    <h1>REGISTER NOW</h1>
                    <h3>It's quick and easy</h3>
                    <div className="form__inputs">
                        <label htmlFor="username" className="form__label">
                            <p>Username</p>
                        </label>
                        <input 
                        id="username"
                        type="text" 
                        name="username" 
                        className="form__input" 
                        placeholder="Enter your username"
                        value={values.username}
                        onChange={handleChange}
                        />  
                        {errors.username && <p className="errors">{errors.username}</p>}                  
                    </div>
                    <div className="form__inputs">
                        <label htmlFor="email" className="form__label">
                            <p>E-mail</p>
                        </label>
                        <input 
                        id="email"
                        type="email" 
                        name="email" 
                        className="form__input" 
                        placeholder="Enter your e-mail"
                        value={values.email}
                        onChange={handleChange}
                        />      
                        {errors.email && <p className="errors">{errors.email}</p>}
                    </div>
                    <div className="form__inputs">
                        <label htmlFor="password" className="form__label">
                            <p>Password</p>
                        </label>
                        <input 
                        id="password"
                        type="password" 
                        name="password" 
                        className="form__input" 
                        placeholder="Create your password"
                        value={values.password}
                        onChange={handleChange}
                        />                    
                        {errors.password && <p className="errors">{errors.password}</p>}
                    </div>
                    <div className="form__inputs">
                        <label htmlFor="password2" className="form__label">
                            <p>Confirm Password</p>
                        </label>
                        <input 
                        id="password2"
                        type="password" 
                        name="password2" 
                        className="form__input" 
                        placeholder="Confirm your password"
                        value={values.password2}
                        onChange={handleChange}
                        />
                        {errors.password2 && <p className="errors">{errors.password2}</p>}
                    </div>
                    {errors.form && <p>{errors.form}</p>}
                    <div className="button__div">
                        <button className="form__input__btn" type="submit" >
                            Add User!
                        </button>
                    </div>
                    <div className="already__sign-in__div">
                        <span className="form__input__login">
                            Already have an account? Login 
                            <a href="#"> here</a>
                        </span>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default FormSignup
